import React, { useState, useEffect } from "react";
import { Form, Input, message } from "antd";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import Loading from "../components/Layouts/Loading";

const Login = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);

  //form submit:
  const submitHandle = async (values) => {
    try {
      setLoading(true);
      const { data } = await axios.post(`/api/users/login`, values);
      message.success("Login Successfull");
      setLoading(false);
      navigate("/");
      localStorage.setItem(
        "user",
        JSON.stringify({ ...data.user, password: "" })
      );
    } catch (error) {
      console.log(error);
      setLoading(false);
      message.error("Invalid email or password");
    }
  };
  //prevent for login user:
  useEffect(() => {
    if (localStorage.getItem("user")) {
      return navigate("/");
    }
  }, [navigate]);
  return (
    <div className=" container m-auto h-screen flex justify-center items-center">
      <div>
        {loading && <Loading />}
        <Form
          layout=" vertical"
          className=" flex flex-col gap-2 bg-slate-300 p-4 rounded-lg"
          onFinish={submitHandle}
        >
          <h1 className="text-xl text-center ">Login Form</h1>

          <Form.Item label="Email" name="email">
            <Input type="email" />
          </Form.Item>
          <Form.Item label="Password" name="password">
            <Input type="password" />
          </Form.Item>
          <Link to="/register">Don't have account? Register </Link>
          <button className=" bg-black w-fit text-white p-2 rounded-lg">
            Login
          </button>
        </Form>
      </div>
    </div>
  );
};

export default Login;
