import React, { useState, useEffect } from "react";
import Layout from "../../components/Layouts/Layout";
import { Form, Input, Modal, Select, message, Table } from "antd";
import { EditOutlined, DeleteOutlined } from "@ant-design/icons";
import moment from "moment";
import axios from "axios";
import Loading from "../../components/Layouts/Loading";

const Home = () => {
  const [openModel, setOpenModel] = useState(false);
  const [loading, setLoading] = useState(false);
  const [allTransection, setAllTransection] = useState([]);
  const [editable, setEditable] = useState(null);
  const [frequency, setFrequency] = useState("7");
  const [type, setType] = useState("all");
  // Define shouldRefreshTable state variable
  const [shouldRefreshTable, setShouldRefreshTable] = useState(false);
  const [form] = Form.useForm();

  // Reset form fields when editable state changes
  useEffect(() => {
    form.resetFields();
  }, [editable, form]);

  //from submit:
  const handleSubmit = async (values) => {
    try {
      const user = JSON.parse(localStorage.getItem("user"));
      setLoading(true);
      if (editable) {
        await axios.post(`/api/transections/edit-transection`, {
          payload: {
            ...values,
            userid: user._id,
          },
          transectionId: editable._id,
        });
        message.success("Transection Saved");
        setShouldRefreshTable(true); // Set shouldRefreshTable to true
      } else {
        await axios.post(`/api/transections/add-transection`, {
          ...values,
          userid: user._id,
        });
        message.success("Transection Saved");
      }
      setLoading(false);
      setOpenModel(false);
      setShouldRefreshTable(true); // Set shouldRefreshTable to true
      form.resetFields(); // <-- clear the form fields
    } catch (error) {
      setLoading(false);
      message.error("Failed to add");
      // console.log(error);
    }
  };

  //Delete transection:
  const handleDelete = async (record) => {
    try {
      setLoading(true);
      await axios.post(`/api/transections/delete-transection`, {
        transectionId: record._id,
      });
      setLoading(false);
      message.success("Deleted Successfully");
      setShouldRefreshTable(true); // Set shouldRefreshTable to true
    } catch (error) {
      setLoading(false);
      // console.log(error);
      message.error("Unable to delete");
    }
  };

  //table transection:
  useEffect(() => {
    //get transections:
    const getAllTransection = async () => {
      try {
        const user = JSON.parse(localStorage.getItem("user"));
        setLoading(true);
        const res = await axios.post(`/api/transections/get-transection`, {
          userid: user._id,
          frequency, // <-- include frequency in request payload
          type,
        });
        setLoading(false);
        setAllTransection(res.data);
        // console.log(res.data);
      } catch (error) {
        setLoading(false);
        // console.log(error);
        message.error("Something went wrong");
      }
    };
    // Check if shouldRefreshTable is true, and fetch transactions again if it is
    if (shouldRefreshTable) {
      getAllTransection(frequency);
      setShouldRefreshTable(false);
    } else {
      getAllTransection(frequency);
    }
  }, [frequency, shouldRefreshTable, type]);

  //ant design tabledata:
  const columns = [
    {
      title: "Date",
      dataIndex: "date",
      render: (text) => <span> {moment(text).format("YYYY-MM-DD")} </span>,
    },
    {
      title: "Type",
      dataIndex: "type",
    },
    {
      title: "Amount",
      dataIndex: "amount",
    },
    {
      title: "Catagory",
      dataIndex: "catagory",
    },
    {
      title: "Reference",
      dataIndex: "reference",
    },

    {
      title: "Actions",
      render: (text, record) => (
        <div className=" flex gap-5">
          <EditOutlined
            title="edit"
            style={{ cursor: "pointer" }}
            onClick={() => {
              setEditable(record);
              setOpenModel(true);
            }}
          />
          <DeleteOutlined
            title="delete"
            onClick={() => {
              handleDelete(record);
            }}
            style={{ cursor: "pointer" }}
          />
        </div>
      ),
    },
  ];

  return (
    <Layout>
      {loading && <Loading />}
      <div className=" container mx-auto mt-5 py-4 w-[100%] px-4 m-auto">
        <div className=" flex items-center justify-between">
          <div className="filter">
            <h6>Select Frequency</h6>
            <Select
              value={frequency}
              onChange={(value) => setFrequency(value)}
              style={{
                width: 120,
              }}
            >
              <Select.Option value="7">Last 1 Week</Select.Option>
              <Select.Option value="30">Last 1 Month</Select.Option>
              <Select.Option value="180">Last 6 Months</Select.Option>
              <Select.Option value="365">Last 1 Year</Select.Option>
              {/* <Select.Option value="custom">Custom</Select.Option> */}
            </Select>
          </div>
          <div className="filter">
            <h6>Select Type</h6>
            <Select
              value={type}
              onChange={(value) => setType(value)}
              style={{
                width: 120,
              }}
            >
              <Select.Option value="all">ALL</Select.Option>
              <Select.Option value="Income">Income</Select.Option>
              <Select.Option value="Expense">Expense</Select.Option>
            </Select>
          </div>
          <button
            title="New Transection"
            className="add rounded-md bg-black text-white py-1 px-3"
            onClick={() => setOpenModel(true)}
          >
            Add New
          </button>
        </div>
      </div>
      <div className=" container">
        <Modal
          style={{ overflow: "hidden", width: "90%", scrollbarWidth: 0 }}
          title={editable ? "Edit Transection" : "Add Transection"}
          open={openModel}
          onCancel={() => {
            setEditable(null); // Reset the editable state
            setOpenModel(false);
            form.resetFields(); // reset form fields on modal close
          }}
          footer={false}
        >
          <Form
            form={form} // <-- add form instance to Form component
            initialValues={editable}
            layout=" vertical"
            onFinish={handleSubmit}
          >
            <Form.Item name="amount" label="Amount" required>
              <Input type="number" />
            </Form.Item>
            <Form.Item name="type" label="Type" required>
              <Select>
                <Select.Option value="Income">Income</Select.Option>
                <Select.Option value="Expense">Expense</Select.Option>
              </Select>
            </Form.Item>
            <Form.Item name="catagory" label="Catagory" required>
              <Select>
                <Select.Option value="salary">Salary</Select.Option>
                <Select.Option value="tip">Tip</Select.Option>
                <Select.Option value="freelencce">Freelence</Select.Option>
                <Select.Option value="food">Food</Select.Option>
                <Select.Option value="shop">Shopping</Select.Option>
                <Select.Option value="travel">Travel</Select.Option>
                <Select.Option value="bills">Bills</Select.Option>
                <Select.Option value="medical">Medical</Select.Option>
                <Select.Option value="fees">Fees</Select.Option>
                <Select.Option value="tax">Tax</Select.Option>
                <Select.Option value="others">Others</Select.Option>
              </Select>
            </Form.Item>
            <Form.Item name="date" label="Date" required>
              <Input type="date" />
            </Form.Item>
            <Form.Item name="reference" label="Reference" required>
              <Input type="text" />
            </Form.Item>

            <div className=" flex items-center justify-end">
              <button
                type="submit"
                className=" bg-blue-400 text-white py-1 px-2"
              >
                SAVE
              </button>
            </div>
          </Form>
        </Modal>
      </div>
      {/* table data */}

      <Table columns={columns} dataSource={allTransection} loading={loading} />
    </Layout>
  );
};

export default Home;
