import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { message } from 'antd'

const Header = () => {
  const navigate = useNavigate();
  const [loginUser, setLoginUser] = useState("");
  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("user"));
    if (user) {
      setLoginUser(user);
    }
  }, []);
  const handleLogout = () => {
    localStorage.removeItem("user");
    message.success('Logout successfully')
    navigate("/login");
  };
  return (
    <div className=" bg-slate-800 text-white py-4 w-[100%] px-4 m-auto">
      <nav className=" flex items-center  container mx-auto ">
        <div className="left flex-1">
          <Link className=" text-xl" to="/">
            Expanse manage
          </Link>
        </div>
       
        <div className="rigth flex-1">
          <ul className="flex gap-2 items-center justify-end">
            <li>
              <p title={loginUser.email} >{loginUser && loginUser.name}</p>
            </li>
            <li>
              <button title="logout"
                className=" bg-white text-slate-800 px-3 py-1 rounded-sm"
                onClick={handleLogout}
              >
                Logout
              </button>
            </li>
          </ul>
        </div>
      </nav>
    </div>
  );
};

export default Header;
