import React, { useEffect, useState } from "react";
import { Form, Input, message } from "antd";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import Loading from "../components/Layouts/Loading";

const Register = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  //form submit:
  const submitHandle = async (values) => {
    try {
      setLoading(true);
      await axios.post(`/api/users/register`, values);

      message.success("Registration Successfull");
      setLoading(false);
      navigate("/login");
    } catch (error) {
      console.log(error);
      setLoading(false);
      message.error("Something went wrong");
    }
  };

  //prevent for login user:
  useEffect(() => {
    if (localStorage.getItem("user")) {
      return navigate("/");
    }
  }, [navigate]);
  return (
    <div className=" container m-auto h-screen flex justify-center items-center">
      <div>
        {loading && <Loading />}
        <Form
          layout=" vertical"
          className=" flex flex-col gap-2 bg-slate-300 p-4 rounded-lg"
          onFinish={submitHandle}
        >
          <h1 className="text-xl text-center ">Register Form</h1>
          <Form.Item label="Name" name="name">
            <Input />
          </Form.Item>
          <Form.Item label="Email" name="email">
            <Input type="email" />
          </Form.Item>
          <Form.Item label="Password" name="password">
            <Input type="password" />
          </Form.Item>
          <Link to="/login">Already registered? </Link>
          <button className=" bg-black w-fit text-white p-2 rounded-lg">
            Register
          </button>
        </Form>
      </div>
    </div>
  );
};

export default Register;
